.btn-wrap button.btn {
    background: #4b384b;
    border-color: #4b384b;
    color: #ffffff;
    font-size: 18px;
    line-height: 1.5;
    padding: 10px 43px;
    border-radius: 25px;
    -webkit-transition-property: background, border;
    -o-transition-property: background, border;
    transition-property: background, border;
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    font-weight: 300;
}
.btn-wrap button.btn:hover {
    background: none;
    border-color: #ffffff;
}

.form-error {
    border-bottom: 1px solid darkred !important;
}

.form-error-message {
    font-size: 12px;
    color: darkred;
}


